.logo-container {
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.logo-container img {
    max-width: 80px;
    height: auto;
}

@media (max-width : 768px) {
    .logo-container img {
        max-width: 120px;
    }
}